.footerBlock {
    background-color: #c3fa68;
    display: flex;
}

.footerContainer {
    border: 1px solid;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
}

.title {
    background-color: #c56ce8;
}

.linksContainer {
    display: flex;
    justify-content: space-evenly;

}
.icon {
    height: 15px;
    width: 15px;
    background-color: #237c88;
    margin: 5px;
}
