.contactsBlock {
    background-color: #c3fa68;
    display: flex;
}

.contactsContainer {
    border: 1px solid;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
}

.title {
    background-color: #c56ce8;
}

.formContainer {
    display: flex;
    height: 100px;
}

.contacts {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: column
}
