.project {
    width: 250px;
    background-color: #61dafb;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    flex-wrap: wrap;
}
.imgContainer {
    height: 200px;
    width: 100%;
    background-color: #237c88;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("https://www.upgrad.com/blog/wp-content/uploads/2020/05/505-React-Project-Ideas.png");
    background-size: cover;
    background-position: center;
}